:root {
  --spacing-smallest: 4px;
  --spacing-half: 5px;
  --spacing-small: 8px;
  --spacing-base: 10px;
  --spacing-large: 15px;
  --spacing-double: 20px;
  --spacing-largest: 35px;
  --border-small: 3px solid var(--shadow-base-color);
  --avatar-width: 42px;
  --message-box-height: 100px;
  --message-box-padding: var(--spacing-base) var(--spacing-large) var(--spacing-largest);
}
